import request from '@/utils/request'

export function fetAudio(data: any) {
  return request({
    url: '/AudioController/audioConvertToText',
    method: 'post',
    data
  })
}

export function fetchOptionList() {
  return request({
    url: '/sys/engineController/getOptionValue',
    method: 'get'
  })
}

export function updateInfo(data: any, id: any) {
  return request({
    url: '/sys/engineParamController/updateEngineParam',
    method: 'put',
    params: id,
    data
  })
}
