/**  */
export default class EngineParam {
  public static build(data: EngineParam) {
    const engineParam: EngineParam = new EngineParam()
    engineParam.engineId = data.engineId
    engineParam.paramKey = data.paramKey
    engineParam.paramValue = data.paramValue
    engineParam.paramType = data.paramType
    engineParam.paramDesc = data.paramDesc
    return engineParam
  }

  /** ID */
  public engineId: string = ''
  /** key */
  public paramKey: string = ''
  /**  */
  public paramValue: string = ''
  /**  */
  public paramType: string = ''
  /**  */
  public paramDesc: string = ''
  /**  */
  readonly [id: string]: string
}
