<template>
  <div class="app-container">
    <div class="filter-container">
      <el-button type="primary" @click="changeConfig">
        
      </el-button>
      <el-button :disabled="recording" type="primary" @click="startRecording">
        
      </el-button>
      <el-button :disabled="!recording" type="primary" @click="stopRecording">
        
      </el-button>
    </div>
    <p>{{ text }}</p>
    <el-dialog :visible.sync="isShowDialog" title="">
      <el-form ref="ruleForm" :model="form">
        <el-form-item :label-width="formLabelWidth" label="">
          <template>
            <el-radio
              v-for="item in optionList"
              :key="item.engineId"
              v-model="engineId"
              :label="item.engineId"
              @change="radioChange"
            >
              {{ item.engineDesc }}
            </el-radio>
          </template>
        </el-form-item>
        <el-form-item v-for="item in paramsList" :key="item.paramKey" :label-width="formLabelWidth" :label="item.paramKey">
          <el-input v-model="item.paramValue" :placeholder="item.paramDesc" />
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="isShowDialog = false">
          
        </el-button>
        <el-button type="primary" @click="handleConfig('ruleForm')">
          
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import WebAudioTrack from '@/utils/recordRtc.js' // 
import { fetAudio, fetchOptionList, updateInfo } from '@/api/demo/recording'
import { fetchInfoList } from '@/api/sysManage/recordingManage'
export default {
  data() {
    return {
      audioTrack: null,
      recording: false,
      text: '',
      optionList: [],
      engineId: 0,
      formLabelWidth: '100px',
      paramsList: [],
      listQuery: {
        page: 1,
        limit: 99,
        _search: false,
        sidx: 'param_key',
        order: 'desc',
        paramKey: '',
        engineId: ''
      },
      isShowDialog: false,
      form: {}
    }
  },
  created() {
    this.getOptionList()
  },
  methods: {
    startRecording() {
      if (!this.engineId) {
        this.$alert('')
        return
      }
      this.captureMicrophone((microphone) => {
        this.audioTrack = WebAudioTrack(microphone, {
          type: 'audio',
          recorderType: WebAudioTrack.StereoAudioRecorder,
          desiredSampRate: 16000
        })
        this.audioTrack.startRecording()
        this.recording = true
      })
    },
    stopRecording() {
      this.audioTrack.stopRecording(() => {
        this.recording = false
        var blob = this.audioTrack.getBlob()
        const reader = new FileReader()
        reader.readAsDataURL(blob)
        reader.onloadend = () => {
          // console.log('reader.result: ' + reader.result)
          // let engine = ''
          // for (let i = 0, length = this.optionList.length; i < length; i++) {
          //   if (this.optionList[i].id === this.id) {
          //     engine = this.optionList[i].engineName
          //     break
          //   }
          // }
          const data = {
            audioBase64: reader.result,
            // engine: 'baidu'
            key: this.engineId
          }

          fetAudio(data).then(responese => {
            this.text = responese.msg
          })
        }
      })
    },
    captureMicrophone(callback) {
      navigator.mediaDevices.getUserMedia({ audio: true }).then(callback).catch(function(error) {
        alert('Unable to access your microphone.')
        console.error(error)
        alert(error)
      })
    },
    getOptionList() {
      fetchOptionList().then(responese => {
        this.optionList = responese.data
        this.engineId = this.optionList.length > 0 ? this.optionList[0].engineId : ''
      })
    },
    radioChange(engineId) {
      this.listQuery.engineId = engineId
      fetchInfoList(this.listQuery).then(responese => {
        this.paramsList = responese.page.list
      })
    },
    changeConfig() {
      if (this.engineId) {
        this.radioChange(this.engineId)
      }
      this.isShowDialog = true
    },
    handleConfig() {
      const config = {}
      this.paramsList.map(item => {
        config[item.paramKey] = item.paramValue
      })
      updateInfo(config, { key: this.engineId }).then(responese => {
        this.isShowDialog = false
      })
    }
  }
}
</script>
