



























import { saveData, fetchEditInfo, updateData } from '@/api/sysManage/recordingManage'
import { Component, Vue, Prop } from 'vue-property-decorator'
import { Engine } from '@/domains/recording'

@Component({
  name: 'ConfigEdit'
})
export default class Edit extends Vue {
  @Prop()
  private id!: string
  @Prop({ default: 'save' })
  private type!: string
  /** form */
  private ruleForm: Engine = new Engine()
  /** form */
  private rules: any = {
    engineName: [
      { required: true, message: 'key', trigger: 'blur' }
    ],
    engineDesc: [
      { required: true, message: '', trigger: 'blur' }
    ]
  }
  private created() {
    if (this.type === 'edit') {
      this.getEditInfo()
    }
  }
  // 
  private submitForm(formName: string) {
    let refs: any = this.$refs[formName]
    refs.validate((valid: boolean) => {
      if (valid) {
        if (this.type === 'edit') {
          this.handleEdit()
        } else {
          this.handleSubmit()
        }
      } else {
        console.log('error submit!!')
        return false
      }
    })
  }
  // 
  private handleSubmit() {
    saveData(this.ruleForm).then(response => {
      this.$emit('msg-from-child', this.type)
    })
  }
  // 
  private handleEdit() {
    updateData(this.ruleForm).then(response => {
      this.$emit('msg-from-child', this.type)
    })
  }
  // 
  private cancel() {
    this.$emit('msg-from-child', 'cancel')
  }
  // 
  private getEditInfo(): void {
    fetchEditInfo(this.id).then((response: any) => {
      const data: Engine = response.list
      this.ruleForm = Engine.build(data)
    }).catch(err => {
      console.log(err)
    })
  }
}
