import request from '@/utils/request'
import { ListQuery } from '@/domains/listQuery'
import { Engine, EngineParam } from '@/domains/recording'

interface ID {
  id: string
}

interface Key {
  key: string
}

export function fetchList(query: ListQuery) {
  return request({
    url: '/sys/engineController',
    method: 'get',
    params: query
  })
}

export function saveData(data: Engine) {
  return request({
    url: '/sys/engineController',
    method: 'post',
    data
  })
}

export function fetchEditInfo(id: string) {
  const url = '/sys/engineController/' + id
  return request({
    url,
    method: 'get'
  })
}

export function updateData(data: Engine) {
  return request({
    url: '/sys/engineController',
    method: 'put',
    data
  })
}

export function deleteData(data: string[]) {
  return request({
    url: '/sys/engineController',
    method: 'delete',
    data
  })
}

export function fetchInfoList(query: ListQuery) {
  return request({
    url: '/sys/engineParamController',
    method: 'get',
    params: query
  })
}

export function saveInfoData(data: EngineParam) {
  return request({
    url: '/sys/engineParamController',
    method: 'post',
    data
  })
}

export function deleteInfoData(data: string[], id: ID) {
  return request({
    url: '/sys/engineParamController',
    method: 'delete',
    data,
    params: id
  })
}

export function fetchEditParamsInfo(id: string, key: string) {
  const url = 'sys/engineParamController/getOptionValue/' + id + '/' + key
  return request({
    url,
    method: 'get'
  })
}

export function updateInfoData(key: Key, data: EngineParam) {
  return request({
    url: '/sys/engineParamController',
    method: 'put',
    data,
    params: key
  })
}
