

































import { saveInfoData, updateInfoData, fetchEditParamsInfo } from '@/api/sysManage/recordingManage'
import { Component, Vue, Prop } from 'vue-property-decorator'
import { EngineParam } from '@/domains/recording'

@Component({
  name: 'RecordingManageInfoEdit'
})
export default class RecordingManageInfo extends Vue {
  @Prop()
  private id!: string
  @Prop({ default: 'save' })
  private type!: string
  @Prop()
  /** Id */
  private engineId!: string
  /** form */
  private ruleForm: EngineParam = new EngineParam()
  /** form */
  private rules: any = {
    key: [
      { required: true, message: '', trigger: 'blur' }
    ],
    value: [
      { required: true, message: '', trigger: 'blur' }
    ]
  }
  /** key */
  private beforeEditKey: string = ''
  private created() {
    this.ruleForm.engineId = this.engineId
    if (this.type === 'edit') {
      this.getEditInfo()
    }
  }
  // 
  private submitForm(formName: string) {
    let refs: any = this.$refs[formName]
    refs.validate((valid: boolean) => {
      if (valid) {
        if (this.type === 'edit') {
          this.handleEdit()
        } else {
          this.handleSubmit()
        }
      } else {
        console.log('error submit!!')
        return false
      }
    })
  }
  // 
  private handleSubmit() {
    saveInfoData(this.ruleForm).then(response => {
      this.$emit('msg-from-child', this.type)
    })
  }
  // 
  private handleEdit() {
    const engineId = {
      key: this.beforeEditKey
    }
    updateInfoData(engineId, this.ruleForm).then(response => {
      this.$emit('msg-from-child', this.type)
    })
  }
  // 
  private cancel() {
    this.$emit('msg-from-child', 'cancel')
  }
  // 
  private getEditInfo(): void {
    fetchEditParamsInfo(this.engineId, this.id).then((response: any) => {
      const data: EngineParam = response.list
      this.ruleForm = EngineParam.build(data)
      this.beforeEditKey = this.ruleForm.paramKey
    }).catch(err => {
      console.log(err)
    })
  }
}
